import { Component, OnInit } from '@angular/core';
import { CustomerService } from '../../../../../auth/_services/customer.service';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';


@Component({
    selector: 'app-customer-list',
    templateUrl: "customer-list.component.html",
    styles: []
})
export class CustomerListComponent implements OnInit {


    constructor(private toastr: ToastrService, private router: Router, private _customerService: CustomerService) { }
    public customerData;
    public searchValue = "";
    public dropDownValue = '';
    p: number = 1;
    ngOnInit() {

        /*user permission*/
        let currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let permission = currentUser.success.permission;
        if (permission.customer_list != 1) {
            this.toastr.error('You have not permission to visit this page.', 'Error Message');
            this.router.navigate(['/']);
        }
        /*user permission*/

        // localStorage.setItem('section', 'customer');

        this._customerService.getAll().subscribe(
            data => {
                // console.log(data);
                this.customerData = data.success;
            }
        );

        // console.log("hello");
    }

    public search() {
        this._customerService.searching({ search_value: this.searchValue, dropdown_value: this.dropDownValue }).subscribe(
            data => {
                console.log(data);
                this.customerData = data.success;
            }
        );
    }

    public customer_delete(del_id) {

        console.log(del_id);

        let value = confirm('Are you want to delete this customer ?');
        if (value) {

            this._customerService.del_customer(del_id).subscribe(
                data => {
                    this.customerData = data.data;
                }
            );
        }

    }


    public changeStatus(customer: any) {

        if (customer.customer_status == 'Active') {
            customer.customer_status = 'Inactive';

            this._customerService.updateStatus(customer.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        } else {
            customer.customer_status = 'Active';
            this._customerService.updateStatus(customer.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        }
    }


}
